<template>
  <div class="login flex column ac js">
    <div class="login-top">
      <h3>绑定手机号</h3>
      <div class="form">
        <div class="form-item">
          <van-field
            v-model="username"
            maxlength="11"
            placeholder="手机号"
            type="tel"
            clearable
            :error-message="errorUser"
          />
        </div>
      </div>
      <van-button class="login-btn" type="default" color="#E84114" block round @click="onSubmit" >下一步</van-button>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Divider, Field, Button } from "vant";
import { getVerificationCode } from "@/api";

Vue.use(Divider);
Vue.use(Field);
Vue.use(Button);

const tel = /^1[3-9]\d{9}$/;

export default {
  data() {
    return {
      username: "",
      errorUser: ""
    };
  },
  methods: {
    onSubmit() {
      if (this.username) {
        if (!tel.test(this.username)) {
          this.errorUser = "请输入正确的手机号";
          return;
        }
        this.errorUser = "";
        const params = {
          mobile: this.username,
          scene: 8
        };
        getVerificationCode(params).then(async res => {
          if (res.success) {
            this.$router.push(`/login/code?mobile=${this.username}&type=bind`);
          }
        });
        return;
      }
      this.errorUser = this.username ? "" : "请输入手机号";
    },
  }
};
</script>

<style lang="less" scoped>
.login {
  height: 100%;
  background: #fff;
  overflow: hidden;
}

.login-top {
  width: 335px;
  margin-top: 40px;
  h3 {
    color: #282828;
    font-size: 19px;
    line-height: 27px;
    margin: 0;
  }
}
.btn {
  width: 300px;
  margin-bottom: 59px;
  img {
    width: 48px;
  }
}

.form {
  margin-top: 10px;
  .van-cell {
    padding: 18px 0;
  }
}

.form-item {
  border-bottom: 1Px solid #dddedd;
  margin-bottom: 8px;
}

.login-btn {
  margin: 40px 0 15px;
}

.other {
  color: #666;
  font-size: 14px;
}
</style>
